<template>
  <el-row type="flex" justify="end">
    <el-col :span="24">
      <ul class="h_nav">
        <li>Home</li>
        <li>Works</li>
        <li>RoadMap</li>
        <li>Team</li>
      </ul>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Nav",
  data() {
    return {
      active: 0, // 当前激活的导航索引
      scrollTop: "", //滚动距离
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll, false);
  },
  destroyed() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    handleScroll() {
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop; //获取滚动距离
      this.scrollTop = scrollTop; //data里return了一个全局的scrollTop
      console.log(scrollTop);
      //这边距离我是通过打印scrollTop大致自己粗略的计算了下，用来判断滚轮滚动相应的距离改变导航栏对应菜单样式
      if (scrollTop > 1115) {
        this.active = 1;
      }
      if (scrollTop > 1850) {
        this.active = 2;
      }
      if (scrollTop < 1000) {
        this.active = 0;
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "../styles/Nav";
</style>
